import User from "./user-model.js";
var user = new User();
export default {
	data() {
		return {
			userInfo: {}
		};
	},
	onShow() {
		const that = this
		that._onLoad()
	},
	methods: {
		_onLoad(callBack) {
			// 使用vuex获取原有的用户信息
			this.userInfo = this.$store.state.userInfo;
			console.clear()
			console.log('------------')
			console.log(this.userInfo)
		},
		// 进入余额页
		goBalancePage(e) {
			user.navigate_to("../balance/balance");
		},
		// 进入帮助页
		goHelpPage(e) {
			user.navigate_to("/pages/help/help");
		},
		goVIP() {
			user.navigate_to("/pages/vip/vip");
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
	  var that = this;
	  that.page = 1;
	  that._onLoad(() => {
	    uni.stopPullDownRefresh();
	  });
	},
	//上拉加载更多
